<template>
  <div class="cart">
    <van-nav-bar title="购物车" fixed />

    <div v-if="isLogin && cartList.length > 0">
      <!-- 购物车开头 -->
      <div class="cart-title">
        <span class="all">共<i>{{ cartTotal }}</i>件商品</span>
        <span @click="isEdit = !isEdit" class="edit">
          <van-icon name="edit" />
          编辑
        </span>
      </div>

      <!-- 购物车列表 -->
      <div class="cart-list">
        <div class="cart-item" v-for="item in cartList" :key="item.goods_id">
          <!-- 使用 :value 绑定，v-model 双向绑定，违反单向数据流 -->
          <van-checkbox @click="toggleCheck(item.goods_id)" :value="item.isChecken"></van-checkbox>
          <div class="show">
            <img :src="item.goods.goods_image" alt="">
          </div>
          <div class="info">
            <span class="tit text-ellipsis-2">{{ item.goods.goods_name }}</span>
            <span class="bottom">
              <div class="price">¥ <span>{{ item.goods.goods_price_min }}</span></div>
              <div class="count-box">
                <!-- v-model 拆分成 :value 和 @input 每次修改数据触发 @input 事件监听，会默认传出修改后的 :value 值，不写标签内的形参，在methods里的函数形参内接受（只能传一个值） -->
                <!-- 既希望保留原本的的形参，又需要通过通用函数传参 => 箭头函数包装一层 -->
                <CountBox @input="(goodsNum) => changeCount(goodsNum,item.goods_id,item.goods_sku_id)" :value="item.goods_num"></CountBox>
              </div>
            </span>
          </div>
        </div>
      </div>

      <div class="footer-fixed">
        <div @click="toggleAllCheck" class="all-check">
          <van-checkbox  :value="isAllChecked"  icon-size="18"></van-checkbox>
          全选
        </div>

        <div class="all-total">
          <div class="price">
            <span>合计：</span>
            <span>¥ <i class="totalPrice">{{ selPrice }}</i></span>
          </div>
          <div v-if="!isEdit" class="goPay" :class="{disabled:selCount === 0}" @click="goPay">结算({{ selCount }})</div>
          <div @click="handelDel" v-else class="delete" :class="{disabled:selCount ===0}">删除</div>
        </div>
      </div>
    </div>
    <div class="empty-cart" v-else>
      <img src="@/assets/empty.png" alt="">
      <div class="tips">
        您的购物车是空的, 快去逛逛吧
      </div>
      <div class="btn" @click="$router.push('/')">去逛逛</div>
    </div>
  </div>
</template>

<script>
import CountBox from '@/components/CountBox.vue'
import { mapActions, mapGetters, mapState } from 'vuex'
export default {
  name: 'CartPage',
  components: {
    CountBox
  },
  computed: {
    ...mapState('cart', ['cartList']),
    ...mapGetters('cart', ['isAllChecked', 'cartTotal', 'selCartList', 'selCount', 'selPrice']),
    isLogin () {
      return this.$store.getters.token
    }
  },
  data () {
    return {
      isEdit: false
    }
  },
  created () {
    // 必须是登录过的用户才能获取购物车列表
    if (this.isLogin) {
      this.$store.dispatch('cart/getCartAction')
    }
  },
  methods: {
    ...mapActions('cart', ['delSelectAction']),
    // 复选框状态切换
    toggleCheck (goodsId) {
      this.$store.commit('cart/toggleCheck', goodsId)
    },

    // 全选状态切换
    toggleAllCheck () {
      // 传递全选框的取反状态
      this.$store.commit('cart/toggleAllCkeck', !this.isAllChecked)
    },

    // 更新购物车商品数量
    changeCount (goodsNum, goodsId, goodsSkuId) {
      // 调用vuex的 action 进行数量修改
      this.$store.dispatch('cart/changeCountAction', {
        goodsId,
        goodsNum,
        goodsSkuId
      })
    },

    // 结算
    goPay () {
      // 判断有没有选中商品
      if (this.selCount > 0) {
        // 有选中的商品才进行结算
        this.$router.push({
          path: '/pay',
          query: {
            mode: 'cart',
            cartIds: this.selCartList.map(item => item.id).join(',')
          }
        })
      }
    },
    // 购物车商品删除
    async handelDel () {
      if (this.selCount === 0) return
      await this.$store.dispatch('cart/delSelectAction')
      this.isEdit = false
    }
  },
  watch: {
    // 监听编辑按钮，实现清空复选框选中状态
    isEdit (value) {
      if (value) {
        this.$store.commit('cart/toggleAllCkeck', false)
      } else {
        this.$store.commit('cart/toggleAllCkeck', true)
      }
    }
  }
}
</script>

<style lang="less" scoped>
// 主题 padding
.cart {
  padding-top: 46px;
  padding-bottom: 100px;
  background-color: #f5f5f5;
  min-height: 100vh;
  .cart-title {
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    font-size: 14px;
    .all {
      i {
        font-style: normal;
        margin: 0 2px;
        color: #fa2209;
        font-size: 16px;
      }
    }
    .edit {
      .van-icon {
        font-size: 18px;
      }
    }
  }

  .cart-item {
    margin: 0 10px 10px 10px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    background-color: #ffffff;
    border-radius: 5px;

    .show img {
      width: 100px;
      height: 100px;
    }
    .info {
      width: 210px;
      padding: 10px 5px;
      font-size: 14px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .bottom {
        display: flex;
        justify-content: space-between;
        .price {
          display: flex;
          align-items: flex-end;
          color: #fa2209;
          font-size: 12px;
          span {
            font-size: 16px;
          }
        }
        .count-box {
          display: flex;
          width: 110px;
          .add,
          .minus {
            width: 30px;
            height: 30px;
            outline: none;
            border: none;
          }
          .inp {
            width: 40px;
            height: 30px;
            outline: none;
            border: none;
            background-color: #efefef;
            text-align: center;
            margin: 0 5px;
          }
        }
      }
    }
  }
}

.footer-fixed {
  position: fixed;
  left: 0;
  bottom: 50px;
  height: 50px;
  width: 100%;
  border-bottom: 1px solid #ccc;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;

  .all-check {
    display: flex;
    align-items: center;
    .van-checkbox {
      margin-right: 5px;
    }
  }

  .all-total {
    display: flex;
    line-height: 36px;
    .price {
      font-size: 14px;
      margin-right: 10px;
      .totalPrice {
        color: #fa2209;
        font-size: 18px;
        font-style: normal;
      }
    }

    .goPay, .delete {
      min-width: 100px;
      height: 36px;
      line-height: 36px;
      text-align: center;
      background-color: #fa2f21;
      color: #fff;
      border-radius: 18px;
      &.disabled {
        background-color: #ff9779;
      }
    }
  }
}
.empty-cart {
    padding: 80px 30px;
    img {
      width: 140px;
      height: 92px;
      display: block;
      margin: 0 auto;
    }
    .tips {
      text-align: center;
      color: #666;
      margin: 30px;
    }
    .btn {
      width: 110px;
      height: 32px;
      line-height: 32px;
      text-align: center;
      background-color: #fa2c20;
      border-radius: 16px;
      color: #fff;
      display: block;
      margin: 0 auto;
    }
  }
</style>
